<template>
  <div class="container">
    <div
      class="sider"
      :style="{
        width: data.siderWidth + data.unit,
        height: data.siderHeight + data.unit,
      }"
    >
      <div
        class="imgList"
        ref="imgList"
        :style="{
          width: data.siderWidth * data.imgList.length + data.unit,
          height: data.siderHeight + data.unit,
        }"
      >
        <img
          :style="{
            width: data.siderWidth + data.unit,
            height: data.siderHeight + data.unit,
          }"
          class="img"
          v-for="(item, index) in data.imgList"
          :key="index"
          :src="item.src"
        />
      </div>
      <div class="hots">
        <div
          class="span"
          :style="{
            backgroundColor: index == data.currentIndex ? 'red' : 'pink',
          }"
          v-for="(item, index) in data.imgList"
          :key="index"
          @click="hotClick(index)"
        ></div>
      </div>
      <div class="back">
        <span class="iconfont icon-ic_navigate_before"></span>
      </div>
      <div class="next">
        <span class="iconfont icon-ic_navigate_next"></span>
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref, reactive, getCurrentInstance, onMounted, onUnmounted } from "vue";
const proxy = getCurrentInstance();
let siderInterval = null;
const siderIntervalDelay = ref(3000);
const siderIntervalCallback = () => {
  if (data.currentIndex + 1 >= data.imgList.length) {
    data.currentIndex = 0;
  } else {
    data.currentIndex++;
  }
  hotClick(data.currentIndex);
};
const siderIntervalStart = () => {
  if (siderInterval) {
    clearInterval(siderInterval);
  }
  siderInterval = setInterval(siderIntervalCallback, siderIntervalDelay.value);
};
const siderIntervalStop = () => {
  clearInterval(siderInterval);
};
let data = reactive({
  imgList: [
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/1.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/2.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/3.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/4.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/5.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/6.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/7.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/8.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/9.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/10.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/11.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/12.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/13.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/14.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/15.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/16.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/17.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/18.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/19.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/20.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/21.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/22.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/23.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/24.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/25.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/26.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/27.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/28.jpg",
    },
    {
      src: "https://raw.githubusercontent.com/2361523525/static/gh-pages/img/29.jpg",
    },
  ],
  currentIndex: 0,
  siderWidth: 10,
  siderHeight: 5,
  unit: "rem",
});
onMounted(() => {
  let sider = proxy.refs.imgList.parentNode;
  let back = sider.querySelector(".back");
  let next = sider.querySelector(".next");
  back.onclick = () => {
    if (data.currentIndex == 0) {
      data.currentIndex = data.imgList.length - 1;
    } else {
      data.currentIndex--;
    }
    hotClick(data.currentIndex);
  };
  next.onclick = () => {
    if (data.currentIndex == data.imgList.length - 1) {
      data.currentIndex = 0;
    } else {
      data.currentIndex++;
    }
    hotClick(data.currentIndex);
  };
  hotClick(data.currentIndex);
  siderIntervalStart();
  sider.onmouseover = () => {
    siderIntervalStop();
    back.style.display = "flex";
    next.style.display = "flex";
  };
  sider.onmouseout = () => {
    siderIntervalStart();
    back.style.display = "none";
    next.style.display = "none";
  };
});

const animate = (el, x, delay, callback) => {
  if (!el) return;
  if (el.xtimer) {
    clearInterval(el.xtimer);
  }
  el.xtimer = setInterval(() => {
    if (el.offsetLeft == x) {
      clearInterval(el.xtimer);
      if (callback) {
        callback();
      }
      return;
    }
    let step = (x - el.offsetLeft) / 10;
    step = step > 0 ? Math.ceil(step) : Math.floor(step);
    el.style.left = el.offsetLeft + step + "px";
  }, delay);
};
const hotClick = (index) => {
  data.currentIndex = index;

  let imgList = proxy.refs.imgList;
  let imgWidth = imgList.parentNode.offsetWidth;
  let x = index * imgWidth > 0 ? -index * imgWidth : 0;
  animate(imgList, x, 10);
};
</script>
<style lang='less' scoped>
.container {
  width: 100%;
  height: 100%;
  display: flex;

  justify-content: center;
  align-items: center;
  .sider {
    position: relative;
    overflow: hidden;
    .hots {
      display: flex;
      width: 80%;
      height: 10px;
      position: absolute;
      bottom: 10px;
      left: 10px;
      justify-content: flex-start;
      align-items: center;
      gap: 5px;
      .span {
        width: 10px;
        height: 10px;
        background-color: rgb(218, 129, 69);
        border-radius: 50%;
      }
    }
    .imgList {
      position: absolute;
      top: 0;
      left: 0;
      .img {
        float: left;
        cursor: pointer;
      }
    }
    .back,
    .next {
      position: absolute;
      transform: translateY(-50%);
      width: 50px;
      height: 50px;
      top: 50%;
      background-color: rgba(0, 0, 0, 0.5);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      .iconfont {
        font-size: 30px;
      }

      color: #fff;
    }
    .back:hover,
    .next:hover {
      background-color: rgba(0, 0, 0, 0.8);
    }
    .back {
      left: 10px;
    }
    .next {
      right: 10px;
    }
  }
}
</style>