<template>
  <div
    class="container"
    :style="{ backgroundImage }"
    @click="randomBackgroundColor"
  ></div>
</template>
<script setup>
import { ref, reactive, defineAsyncComponent, onMounted } from "vue";
let backgroundImage = ref("");
let timeout = null;
onMounted(() => {
  timeout = setInterval(() => {
    randomBackgroundColor();
  }, 2000);
});
let randomColor = () => {
  let result = Math.random().toString(16).substring(2, 8);
  return result.length == 6
    ? result
    : result +
        Math.random()
          .toString(16)
          .substring(2, 6 - result.length);
};
let randomBackgroundColor = () => {
  backgroundImage.value = `linear-gradient(${Math.floor(
    Math.random() * 361
  )}deg, #${randomColor()}, #${randomColor()})`;
};
</script>
<style lang='less' scoped>
.container {
  width: 100%;
  height: 100%;
  transition: background-image 3s;
}
</style>