 <template>
  <template v-for="(item, index) in props.navs">
    <template v-if="item.children">
      <el-sub-menu>
        <template #title>
          {{ item.name }}
        </template>
        <submenu
          :parentPath="props.parentPath + '/' + item.path"
          :navs="item.children"
        ></submenu>
      </el-sub-menu>
    </template>
    <template v-else>
      <el-menu-item :index="props.parentPath + '/' + item.path">
        {{ item.name }}
      </el-menu-item>
    </template>
  </template>
</template>
 <script setup>
import { defineProps } from "vue";
import submenu from "@/components/submenu";
const props = defineProps({
  parentPath: {
    default: "",
    type: String,
  },
  navs: Array,
});
console.log(props.navs);
</script>

