<template>
  <div :class="{ main: true, activemain: checked }">
    <div class="outside" ref="outside">
      <div class="inside">
        <span>{{ bs == 3.99 ? 4 : bs + " " }}x</span>
      </div>
    </div>
    <div
      @click="checked = !checked"
      :class="{ switch: true, switchactive: checked }"
    >
      <input type="checkbox" style="display: none" :checked="checked" /><span
        class="btn"
      ></span>
    </div>
  </div>
</template>
<script setup>
import {
  ref,
  reactive,
  computed,
  toRefs,
  watch,
  getCurrentInstance,
  toRaw,
  onMounted,
} from "vue";
const proxy = getCurrentInstance();
let outside;
let inside;
let bs = ref(0);
let checked = ref(false);
let mousemove = (e) => {
  console.log(e);
  inside.style.height = e.offsetY + "px";
  bs.value = ((e.offsetY / outside.clientHeight) * 4).toFixed(2);
};
onMounted(() => {
  outside = proxy.refs.outside;
  inside = outside.children[0];
  outside.addEventListener("mouseover", (e) => {
    outside.addEventListener("mousemove", mousemove);
  });
  outside.addEventListener("mouseout", (e) => {
    outside.removeEventListener("mousemove", mousemove);
  });
});
</script>
<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
.activemain {
  background-image: linear-gradient(135deg, #be4b79, #7bd125) !important;
}
.main {
  background-image: linear-gradient(135deg, #17ead9, #6078ea);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .outside {
    width: 60px;
    height: 400px;
    background-color: #fff;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
    .inside {
      height: 0px;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      left: 0;
      right: 0;
      top: 0;
      background-image: linear-gradient(135deg, #f02fc2, #6094ea);
      transition: height 0.25s;
    }
  }
  .switch {
    cursor: pointer;
    width: 60px;
    height: 30px;
    border-radius: 15px;
    background-color: #ff4949;
    display: flex;
    align-items: center;
    padding: 2px;
    position: relative;
    transition: all 0.25s;
    .btn {
      position: absolute;
      left: 0;
      display: block;
      box-sizing: border-box;
      width: 26px;
      height: 26px;
      border-radius: 13px;
      transition: all 0.25s;
      background-color: #fff;
    }
  }
  .switchactive {
    background-color: #1a8acf !important;
    .btn {
      left: 100% !important;
      transform: translateX(-106%) !important;
    }
  }
}
</style>