<template>
  <div class="container">
    <div class="sider" ref="sider">
      <div class="siderBar" ref="siderBar"></div>
    </div>
    <span class="value">{{ result }}</span>
  </div>
</template>
<script setup>
import { ref, reactive, getCurrentInstance, onMounted } from "vue";
const proxy = getCurrentInstance();
const result = ref(0);
