const siderInit = (sider, siderBar) => {
  sider.onmousedown = (e) => {
    e.stopPropagation();
    siderBar.style.left = e.offsetX + "px";
    result.value = Math.floor((e.offsetX / sider.offsetWidth) * 100.0);
  };
  siderBar.onmousedown = (e) => {
    e.stopPropagation();
    let siderClientX =
      e.pageX - (e.target.offsetLeft + e.offsetX - e.target.offsetWidth / 2);

    siderBar.onmousemove = (e) => {
      siderBar.style.left =
        e.pageX - siderClientX > 0
          ? e.pageX - siderClientX < sider.offsetWidth
            ? e.pageX - siderClientX + "px"
            : sider.offsetWidth + "px"
          : 0 + "px";
      result.value = Math.floor(
        (siderBar.offsetLeft / sider.offsetWidth) * 100.0
      );
    };
    document.onmousemove = (e) => {
      siderBar.style.left =
        e.pageX - siderClientX > 0
          ? e.pageX - siderClientX < sider.offsetWidth
            ? e.pageX - siderClientX + "px"
            : sider.offsetWidth + "px"
          : 0 + "px";
      result.value = Math.floor(
        (siderBar.offsetLeft / sider.offsetWidth) * 100.0
      );
    };
  };
  siderBar.onmouseup = (e) => {
    siderBar.onmousemove = null;
    document.onmousemove = null;
  };
  document.onmouseup = (e) => {
    siderBar.onmousemove = null;
    document.onmousemove = null;
  };
};
onMounted(() => {
  siderInit(proxy.refs.sider, proxy.refs.siderBar);
});
</script>
<style lang='less' scoped>
.container {
  width: 100%;
  height: 100%;

  background: linear-gradient(72deg, #4cbfb3, #dd56cb);
  display: flex;
  justify-content: center;
  align-items: center;
  .sider {
    background-color: rgb(141, 165, 100);
    width: 20%;
    height: 10px;
    border-radius: 10px;
    position: relative;
    cursor: pointer;
    .siderBar {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(-50%, -50%);
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-color: pink;
    }
  }
}
