import { createRouter, createWebHashHistory } from "vue-router";
import index from "@/components/index";
import HelloWord from "@/components/HelloWorld";
import article from "@/components/article";
import demo1 from "@/components/demo1";
import demo2 from "@/components/demo2";
import demo3 from "@/components/demo3";
import demo4 from "@/components/demo4";
import demo5 from "@/components/demo5";
import demo6 from "@/components/demo6";

import test1 from "@/components/test1";
import test2 from "@/components/test2";
import test3 from "@/components/test3";
import test4 from "@/components/test4";
import test5 from "@/components/test5";
import test6 from "@/components/test6";
import test7 from "@/components/test7";
import test8 from "@/components/test8";
let routes = [
  { path: "/", redirect: "/index" },
  {
    path: "/index",
    component: index,
    name: "index",
    redirect: "/index/demo1",
    children: [
      { path: "HelloWord", component: HelloWord, name: "HelloWord" },
      { path: "article", component: article, name: "article1" },
      { path: "demo1", component: demo1, name: "demo1" },
      { path: "demo2", component: demo2, name: "demo2" },
      {
        path: "demo3",
        component: demo3,
        name: "demo3",
        meta: { title: "aaaaa", icon: "add" },
      },
      { path: "demo4", component: demo4, name: "demo4" },
      { path: "demo5", component: demo5, name: "demo5" },
      {
        path: "demo6",
        component: demo6,
        name: "demo6",
        meta: { title: "aaaaa", icon: "add" },
      },
      { path: "test1", component: test1, name: "test1" },
      { path: "test2", component: test2, name: "test2" },
      { path: "test3", component: test3, name: "test3" },
      { path: "test4", component: test4, name: "test4" },
      { path: "test5", component: test5, name: "test5" },
      { path: "test6", component: test6, name: "test6" },
      { path: "test7", component: test7, name: "test7" },
      { path: "test8", component: test8, name: "test8" },
    ],
  },
];
let router = createRouter({
  history: createWebHashHistory(),
  routes,
});
export default router;

export const navs = function () {
  let array = [];
  routes.forEach((el, i) => {
    if (el.path != `/`) {
      array.push(el);
    }
  });
  return array;
};
