<template>
  <div class="main">
    <div style="height: 600px; position: relative">
      <div
        style="
          width: 500px;
          height: 400px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          z-index: 100;
        "
      >
        <VueCropper
          ref="vueCropper"
          v-bind="vueCropperData"
          @realTime="realTime"
        ></VueCropper>
        <div
          v-if="data.previews"
          class="show-preview"
          :style="{
            width: data.previews.w + 'px',
            height: data.previews.h + 'px',
            overflow: 'hidden',
            margin: '5px',
            position: 'absolute',
            top: '50%',
            right: '-50px',
            transform: 'translate(100%, -50%)',
            zoom: 2,
          }"
        >
          <div :style="data.previews.div">
            <img :src="data.previews.url" :style="data.previews.img" />
          </div>
        </div>
      </div>
      <div
        v-bind="data.dropzone.getRootProps()"
        ref="dropzone"
        class="dropzone"
      >
        <input v-bind="data.dropzone.getInputProps()" />

        <!-- <p v-if="isDragActive">Drop the files here ...</p>
      <p v-else>Drag 'n' drop some files here, or click to select files</p> -->
      </div>
    </div>

    <div style="width: 100%">
      <button ref="dropzoneOpen" class="btn" @click="data.dropzone.open">
        open
      </button>
      <button @click="clear">清空</button
      ><button @click="disabled">禁用</button>
    </div>

    <div
      v-show="data.preview"
      style="
        position: fixed;
        background-color: pink;
        background-color: rgba(0, 0, 0, 0.6);
        display: flex;
        justify-content: center;
        align-items: center;
        inset: 0;
        z-index: 101;
      "
    >
      <img
        style="
          position: absolute;
          top: 10px;
          right: 10px;
          width: 30px;
          cursor: pointer;
        "
        @click="previewClose"
        src="data:image/svg+xml;base64,"
      />
      <img :src="data.previewUrl" alt="" />
    </div>
  </div>
  <div style="display: flex; align-items: center; justify-content: center">
    <input
      type="file"
      ref="uploadimg"
      style="display: none"
      @change="inputChange($event, 1)"
    />
    <button @click="uploadimg">上传图片</button
    ><button v-if="cropperState" @click="startCrop">开始裁剪</button>
    <button v-else @click="stopCrop">结束裁剪</button>
    <button @click="download('base64')">下载</button>
    <button @click="preview('base64')">预览</button>
    <button @click="clearCrop">清除</button>
    <button>向左旋转</button>
  </div>
</template>

<script setup name="helloword">
import "vue-cropper/dist/index.css";
import { VueCropper } from "vue-cropper";
import { reactive, ref, getCurrentInstance } from "vue";

import { computed, toRefs, watch } from "vue";
import { useStore } from "vuex";
import { useDropzone } from "vue3-dropzone";
let store = useStore();
let proxy = getCurrentInstance();
let option = reactive({
  onDrop(acceptFiles, rejectReasons) {
    console.log(arguments);
    console.log(acceptFiles);
    console.log(rejectReasons);
    if (rejectReasons.length > 0) {
      alert(`只能上传这些类型的文件：${option.accept.join(",")}`);
    }
    if (acceptFiles.length > 0) {
      let reader = new FileReader();
      reader.onload = (e) => {
        if (typeof e.target.result === "object") {
          // 把Array Buffer转化为blob 如果是base64不需要

          data.imgs = [
            window.URL.createObjectURL(new Blob([e.target.result])),
            ...data.imgs,
          ];
          vueCropperData.img = window.URL.createObjectURL(
            new Blob([e.target.result])
          );
        } else {
          data.imgs = [e.target.result, ...data.imgs];
          vueCropperData.img = e.target.result;
        }
      };
      // 转化为base64
      for (const key in acceptFiles) {
        reader.readAsDataURL(acceptFiles[key]);
      }

      // 转化为blob
      // reader.readAsArrayBuffer(file);
    }
  },
  accept: ["image/*"],
  disabled: false,
  multiple: 0,
});
let data = reactive({
  imgs: computed({
    get() {
      return store.state.imgs || [];
    },
    set(value) {
      store.commit("SET_IMGS", value);
    },
  }),
  dropzone: useDropzone({
    ...option,
  }),
  previews: null,
  preview: false,
  previewUrl: "",
});

const clear = () => {
  data.imgs = [];
};
const disabled = () => {
  if (proxy.refs.dropzone.style.pointerEvents != "none") {
    proxy.refs.dropzone.style.pointerEvents = "none";
    proxy.refs.dropzone.style.userSelect = "none";
    proxy.refs.dropzoneOpen.disabled = true;
  } else {
    proxy.refs.dropzone.style.pointerEvents = "unset";
    proxy.refs.dropzone.style.userSelect = "unset";
    proxy.refs.dropzoneOpen.disabled = false;
  }
};

const vueCropperData = reactive({
  img: "",
  fixedBox: false,
  canMove: true,
  canMoveBox: true,
  mode: "contain", //图片默认渲染方式
  enlarge: 1, //图片根据截图框输出比例倍数
  maxImgSize: 2000, //限制图片最大宽度和高度
  infoTrue: false, //true 为展示真实输出图片宽高 false 展示看到的截图框宽高
  high: true, //是否按照设备的dpr 输出等比例图片
  centerBox: false, //截图框是否被限制在图片里面
  original: false, //上传图片按照原始比例渲染
  full: true, //是否输出原图比例的截图
  fixedNumber: [1, 1], //截图框的宽高比例
  fixed: false, //是否开启截图框宽高固定比例
  autoCropWidth: "80%", //自动截图框宽度
  autoCropHeight: "80%", //自动截图框高度
  autoCrop: true, //是否自动生成截图框
  canScale: true, //图片是否允许滚轮缩放
  info: true, //裁剪框的大小信息
  outputSize: 1, //裁剪生成图片的质量
  outputType: "png", //裁剪生成图片的类型
});
const cropperState = ref(true);

const uploadimg = () => {
  proxy.refs.uploadimg.click();
};
const inputChange = (e, num) => {
  // 上传图片
  // this.option.img
  var file = e.target.files[0];
  if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
    alert("图片类型必须是.gif,jpeg,jpg,png,bmp中的一种");
    return false;
  }
  var reader = new FileReader();
  reader.onload = (e) => {
    let data;
    if (typeof e.target.result === "object") {
      // 把Array Buffer转化为blob 如果是base64不需要
      data = window.URL.createObjectURL(new Blob([e.target.result]));
    } else {
      data = e.target.result;
    }

    vueCropperData.img = data;
  };
  // 转化为base64

  reader.readAsDataURL(file);
  // 转化为blob
  // reader.readAsArrayBuffer(file);
};
const startCrop = () => {
  if (!vueCropperData.img) {
    alert("请先上传图片");
    return;
  }
  proxy.refs.vueCropper.startCrop();
  cropperState.value = false;
};
const stopCrop = () => {
  proxy.refs.vueCropper.stopCrop();
  cropperState.value = true;
};
const clearCrop = () => {
  proxy.refs.vueCropper.clearCrop();
  cropperState.value = true;
};
const realTime = (a) => {
  data.previews = a;
};
const download = (type) => {
  // event.preventDefault()
  let aLink = document.createElement("a");
  aLink.download = "demo";
  // 输出
  console.log(proxy.refs.vueCropper);
  if (type === "blob") {
    proxy.refs.vueCropper.getCropBlob((data) => {
      aLink.href = window.URL.createObjectURL(data);
      aLink.click();
    });
  } else {
    proxy.refs.vueCropper.getCropData((data) => {
      aLink.href = data;
      aLink.click();
    });
  }
};
const preview = (type) => {
  if (type === "blob") {
    proxy.refs.vueCropper.getCropBlob((d) => {
      data.previewUrl = window.URL.createObjectURL(d);
      data.preview = true;
    });
  } else {
    proxy.refs.vueCropper.getCropData((d) => {
      data.previewUrl = d;
      data.preview = true;
    });
  }
};
const previewClose = () => {
  data.preview = false;
  data.previewUrl = "";
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
