<template>
  <div class="container" ref="container">
    <div class="box1"></div>
    <div class="box2"></div>
    <div class="box3">
      <div class="span">
        <button @click="goto500">对对对</button>
        <button @click="gotoblue">对对对</button>
        <button @mouseenter="mouseenter" @mouseleave="mouseleave">
          对对对
        </button>
      </div>
    </div>
    <div class="box4"></div>
    <div class="box5"></div>
    <div class="box6"></div>
    <div class="box7"></div>
    <div class="box8"></div>
  </div>
</template>
<script setup>
import { ref, reactive, onMounted, onUnmounted, getCurrentInstance } from "vue";
const proxy = getCurrentInstance();
onMounted(() => {
  const container = proxy.refs.container;
  let box3span = container.querySelector(".box3 .span");
  console.log(box3span);
  let box3offsetTop = box3span.parentNode.offsetTop;
  let main = proxy.refs.container.parentNode;
  let visualHeight = main.scrollHeight - main.clientHeight;
  main.onscroll = (e) => {
    if (e.target.scrollTop > box3offsetTop) {
      box3span.style.position = "fixed";
      box3span.style.top = "60px";
      box3span.style.right = "37px";
    } else {
      box3span.style.position = "absolute";
      box3span.style.top = "0px";
      box3span.style.right = "-60px";
    }
    if (e.target.scrollTop > visualHeight * 0.5) {
      if (box3span.querySelector(".btn")) {
        return;
      }
      let btn = document.createElement("button");
      btn.classList.add("btn");
      btn.innerText = "回到顶部";
      btn.onclick = () => {
        scrollTo(main, 0);
      };
      box3span.appendChild(btn);
    } else {
      if (box3span.querySelector(".btn")) {
        box3span.removeChild(box3span.querySelector(".btn"));
      }
    }
  };
});
const scrollTo = (el, scrollTop, callback) => {
  if (el.scrollTop == scrollTop) {
    return;
  }
  if (el.scrollTopTimer) {
    clearTimeout(el.scrollTopTimer);
  }
  el.scrollTopTimer = setInterval(() => {
    console.log(el.scrollTop + "  " + scrollTop);
    if (
      el.scrollTop == scrollTop ||
      el.scrollTop >= el.scrollHeight - el.clientHeight
    ) {
      clearInterval(el.scrollTopTimer);
      if (callback) {
        callback();
      }
      return;
    }
    let step = Math.ceil(Math.abs(el.scrollTop - scrollTop) / 10);
    console.log(step);
    el.scrollTop =
      el.scrollTop > scrollTop ? el.scrollTop - step : el.scrollTop + step;
  }, 30);
};
const goto500 = () => {
  scrollTo(proxy.refs.container.parentNode, 800, () => {
    alert("滚动完成");
  });
};
const gotoblue = () => {
  console.log(1);
  scrollTo(
    proxy.refs.container.parentNode,
    proxy.refs.container.querySelector(".box4").offsetTop
  );
};
const mouseenter = () => {
  scrollTo(
    proxy.refs.container.parentNode,
    proxy.refs.container.querySelector(".box5").offsetTop,
    () => {
      alert("已经滚动到box5");
    }
  );
};
const mouseleave = () => {
  scrollTo(
    proxy.refs.container.parentNode,
    proxy.refs.container.querySelector(".box6").offsetTop,
    () => {
      alert("已经滚动到box6");
    }
  );
};
</script>
<style lang='less' scoped>
.container {
  height: 100%;
  width: 100%;
  .box1 {
    height: 300px;
    margin: 0 80px;
    margin-bottom: 40px;
    background-color: red;
  }
  .box2 {
    height: 300px;

    margin: 0 80px;
    margin-bottom: 40px;
    background-color: green;
  }
  .box3 {
    height: 300px;
    margin: 0 80px;
    background-color: blue;
    margin-bottom: 40px;
    position: relative;
    .span {
      position: absolute;
      top: 0;
      right: -60px;
      width: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      background-color: rgb(170, 26, 26);
      button {
        display: block;
      }
    }
  }
  .box4 {
    height: 300px;
    margin: 0 80px;
    background-color: yellow;
    margin-bottom: 40px;
  }
  .box5 {
    height: 300px;
    margin: 0 80px;
    background-color: orange;
    margin-bottom: 40px;
  }
  .box6 {
    height: 300px;
    margin: 0 80px;
    background-color: purple;
    margin-bottom: 40px;
  }
  .box7 {
    height: 300px;
    margin: 0 80px;
    background-color: brown;
    margin-bottom: 40px;
  }
}
</style>