<template>
  <div :class="{ main: true }">
    <div class="form">
      <div
        ref="mask"
        v-show="maskShow"
        :class="{ mask: true, in: isIn, out: !isIn }"
      ></div>
      <div class="bd" @mouseover="over" @mouseout="out">
        <h1>登录</h1>
      </div>
    </div>
  </div>
</template>
<script setup>
import {
  ref,
  reactive,
  computed,
  toRefs,
  watch,
  getCurrentInstance,
  toRaw,
  onMounted,
} from "vue";
const proxy = getCurrentInstance();
const isIn = ref(false);
const maskShow = ref(false);
let timeout;
const over = (e) => {
  let mask = proxy.refs.mask;
  mask.style.top = e.offsetY + "px";
  mask.style.left = e.offsetX + "px";
  maskShow.value = true;
  isIn.value = true;
  console.log(e);
};
const out = (e) => {
  let mask = proxy.refs.mask;
  mask.style.top = e.offsetY + "px";
  mask.style.left = e.offsetX + "px";
  isIn.value = false;
  timeout = setTimeout(() => {
    maskShow.value = false;
  }, 1000);
};

onMounted(() => {});
</script>
<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}

.main {
  position: absolute;
  background-image: linear-gradient(135deg, #17ead9, #6078ea);
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  .form {
    width: 300px;
    height: 400px;
    background-color: #1890ff;
    border-radius: 15px;
    position: relative;
    overflow: hidden;

    .mask {
      top: 0;
      left: 0;
      position: absolute;
      width: 0px;
      height: 0px;
      background-color: pink;
      z-index: 2;
      border-radius: 50%;
      transform: translate(-50%, -50%);
    }
    .in {
      animation: in 1s ease-in-out forwards;
    }
    .out {
      animation: out 1s ease-in-out forwards;
    }

    .bd {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 3;
      background-color: transparent;
    }
  }
}
@keyframes in {
  0% {
    width: 0px;
    height: 0px;
  }
  100% {
    width: 1200px;
    height: 1200px;
  }
}
@keyframes out {
  0% {
    width: 1200px;
    height: 1200px;
  }
  100% {
    width: 0px;
    height: 0px;
  }
}
</style>