<template>
  <div class="container" :style="{ backgroundImage }">
    <img
      :class="{ active: `url(${item})` == backgroundImage }"
      v-for="item in data.imgs"
      :src="item"
      @click="handleChangeBackgroundImg(item)"
    />
  </div>
</template>
<script setup>
import { ref, reactive, defineAsyncComponent, onMounted } from "vue";
const data = reactive({
  imgs: [],
});
let backgroundImage = ref("");
let timeout = null;
let timeoutCallback = () => {
  changeBackgroundImg(data.imgs[Math.floor(Math.random() * data.imgs.length)]);
};
onMounted(() => {
  import("@/assets/1.jpg").then((res) => {
    console.log((backgroundImage.value = `url(${res.default})`));
  });
  for (let i = 1; i < 7; i++) {
    import("@/assets/" + i + ".jpg").then((res) => {
      data.imgs.push(res.default);
    });
  }
  timeout = setInterval(timeoutCallback, 3000);
});
const changeBackgroundImg = (img) => {
  backgroundImage.value = `url(${img})`;
};
const handleChangeBackgroundImg = (img) => {
  changeBackgroundImg(img);
  clearInterval(timeout);
  timeout = setInterval(timeoutCallback, 3000);
};
</script>
<style lang='less' scoped>
.container {
  width: 100%;
  height: 100%;
  transition: background-image 3s;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  img {
    width: 200px;
    cursor: pointer;
  }
  .active {
    border: 1px solid red;
  }
  transition: background-image 1s;
}
</style>