<template>
  <div>
    <div
      v-bind="getRootProps()"
      ref="dropzone"
      style="height: 200px; background: pink"
    >
      <input v-bind="getInputProps()" />
      <!-- <p v-if="isDragActive">Drop the files here ...</p>
      <p v-else>Drag 'n' drop some files here, or click to select files</p> -->
    </div>
    <button ref="dropzoneOpen" class="btn" @click="open">open</button>
    <button @click="clear">清空</button><button @click="disabled">禁用</button>
    <div
      style="
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        flex-wrap: wrap;
      "
    >
      <div style="width: 300px" v-for="img in data.imgs">
        <img :src="img" style="width: 100%" />
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, computed, toRefs, watch, getCurrentInstance } from "vue";
import { useStore } from "vuex";
import { useDropzone } from "vue3-dropzone";

export default {
  name: "UseDropzoneDemo",
  setup() {
    let store = useStore();
    let proxy = getCurrentInstance();
    let option = reactive({
      onDrop(acceptFiles, rejectReasons) {
        console.log(arguments);
        console.log(acceptFiles);
        console.log(rejectReasons);
        if (rejectReasons.length > 0) {
          alert(`只能上传这些类型的文件：${option.accept.join(",")}`);
        }
        if (acceptFiles.length > 0) {
          let reader = new FileReader();
          reader.onload = (e) => {
            if (typeof e.target.result === "object") {
              // 把Array Buffer转化为blob 如果是base64不需要

              data.imgs = [
                ...data.imgs,
                window.URL.createObjectURL(new Blob([e.target.result])),
              ];
            } else {
              data.imgs = [e.target.result, ...data.imgs];
            }
          };
          // 转化为base64
          for (const key in acceptFiles) {
            reader.readAsDataURL(acceptFiles[key]);
          }

          // 转化为blob
          // reader.readAsArrayBuffer(file);
        }
      },
      accept: ["image/*"],
      disabled: false,
      multiple: 0,
    });
    let data = reactive({
      imgs: computed({
        get() {
          return store.state.imgs || [];
        },
        set(value) {
          store.commit("SET_IMGS", value);
        },
      }),
      dropzone: useDropzone({
        ...option,
      }),
    });

    const clear = () => {
      data.imgs = [];
    };
    const disabled = () => {
      if (proxy.refs.dropzone.style.pointerEvents != "none") {
        proxy.refs.dropzone.style.pointerEvents = "none";
        proxy.refs.dropzone.style.userSelect = "none";
        proxy.refs.dropzoneOpen.disabled = true;
      } else {
        proxy.refs.dropzone.style.pointerEvents = "unset";
        proxy.refs.dropzone.style.userSelect = "unset";
        proxy.refs.dropzoneOpen.disabled = false;
      }
    };
    return {
      ...toRefs(data.dropzone),
      data,
      clear,
      disabled,
    };
  },
};
</script>