<template>
  <div ref="main1"></div>
  <button @click="change">修改</button>
  <div style="position: relative">
    的防控力量分开开{{ option.text
    }}<input
      type="text"
      style="
        border: none;
        outline: none;
        height: 0;
        width: 0;
        position: absolute;
        left: -1000px;
      "
      ref="input"
    /><img
      style="
        cursor: pointer;
        position: absolute;
        top: 10px;
        right: 10px;
        width: 20px;
        height: 20px;
      "
      src="data:image/svg+xml;base64,"
      alt=""
      @click="getClipboardContents"
    />
    <button @click="read">读取剪贴板</button>
  </div>
  <div style="display: flex; justify-content: center; align-items: center">
    <img
      @click="imgPreview"
      v-on:wheel="whell"
      :src="option.url || defaulturl"
      style="width: 500px; overflow: hidden; cursor: pointer"
      alt=""
    />
  </div>
  <div
    ref="showPreview"
    @click="hidePreview"
    style="
      position: fixed;
      inset: 0;
      background-color: rgba(0, 0, 0, 0.5);
      display: none;
      justify-content: center;
      align-items: center;
      z-index: 10;
    "
  ></div>
  <video :src="option.mp4" controls autoplay></video>
</template>
<script setup>
import {
  ref,
  reactive,
  computed,
  toRefs,
  watch,
  getCurrentInstance,
  toRaw,
  onMounted,
  onUnmounted,
} from "vue";
import * as echarts from "echarts";
let proxy = getCurrentInstance();
let defaulturl = `data:image/svg+xml;base64,`;
let myChart;
let PictureSuffixs = ["png", "jpg", "jpeg", "gif", "bmp", "webp"];
let showPreview = ref(false);
const option = reactive({
  mp4: "",
  text: "",
  url: "",
  option: {
    title: {
      text: "ECharts 入门示例",
    },
    tooltip: {},
    xAxis: {
      data: ["衬衫", "羊毛衫", "雪纺衫", "裤子", "高跟鞋", "袜子"],
    },
    yAxis: {},
    series: [
      {
        name: "销量",
        type: "bar",
        data: [5, 20, 36, 10, 10, 20],
      },
    ],
  },
});
onMounted(() => {
  myChart = echarts.init(proxy.refs.main1, null, {
    width: 800,
    height: 500,
  });
  window.onresize = function () {
    myChart.resize({
      width: 400,
      height: 250,
    });
  };

  // 绘制图表
  myChart.setOption(option.option);
  document.onpaste = function (e) {
    console.dir(e.clipboardData.files);
    if (e.clipboardData.files.length > 0) {
      let file = e.clipboardData.files[0];
      let suffix = file.name.split(".")[1];
      if (PictureSuffixs.includes(suffix)) {
        let reader = new FileReader();
        reader.onload = (e) => {
          option.url = e.target.result;
        };
        reader.readAsDataURL(file);
      }
      if (suffix === "mp4") {
        let reader = new FileReader();
        reader.onload = (e) => {
          option.mp4 = e.target.result;
        };
        reader.readAsDataURL(new Blob([file], { type: "video/mp4" }));
      }
    }

    // read();
  };
  document.oncopy = async function (e) {
    e.preventDefault();
    try {
      const text = await navigator.clipboard.writeText("1111111");
      console.log("Pasted content: ", text);
    } catch (err) {
      console.error("Failed to read clipboard contents: ", err);
    }
  };
});
onUnmounted(() => {
  document.onpaste = null;
  document.oncopy = null;
  myChart.dispose();
});
const change = () => {
  option.option.series[0].data = getUniqueNums(1, 10000, 6);
  myChart.setOption(option.option);
};
const getUniqueNums = (start, end, n) => {
  let max = start > end ? start : end;
  let min = start < end ? start : end;

  let zdscgs = max - min + 1;
  let result = [];
  while (true) {
    if (result.length == n || result.length == zdscgs) {
      break;
    }
    let number = Math.round(Math.random() * (max - min) + min);
    if (!result.includes(number)) {
      result.push(number);
    }
  }
  return result;
};
async function getClipboardContents() {
  try {
    const text = await navigator.clipboard.writeText("1111111");
    console.log("Pasted content: ", text);
  } catch (err) {
    console.error("Failed to read clipboard contents: ", err);
  }
}
async function read() {
  try {
    option.text = await navigator.clipboard.read();
    console.log(option.text);
  } catch (err) {
    console.error("Failed to read clipboard contents: ", err);
  }
}
const whell = (e) => {
  e.preventDefault();
  if (e.wheelDelta == 0) {
    return;
  }

  if (e.wheelDeltaY > 0) {
    e.target.style.width = e.target.clientWidth * 1.1 + "px";
  } else {
    e.target.style.width = e.target.clientWidth * 0.9 + "px";
  }
};
const imgPreview = (e) => {
  proxy.refs.showPreview.style.display = "flex";
  proxy.refs.showPreview.innerHTML = "";
  let img = document.createElement("img");
  img.src = e.target.src;
  img.onwheel = whell;
  img.onclick = () => {
    proxy.refs.showPreview.style.display = "none";
  };
  proxy.refs.showPreview.appendChild(img);
};
const hidePreview = (e) => {
  e.target.style.display = "none";
};
</script>
<style lang="less" scope>
</style>