import { createStore } from "vuex";
import createPersistedstate from "vuex-persistedstate";
import {} from "vue";
const appStore = createStore({
  state: { imgs: [] },
  mutations: {
    SET_IMGS: (state, imgs) => {
      state.imgs = imgs;
    },
  },
  actions: {
    SET_IMGS({ commit }) {
      commit("SET_IMGS", "");
    },
  },
  getters: {
    imgs: (state) => state.imgs,
  },
  plugins: [
    createPersistedstate({
      key: "saveInfo",
      paths: ["imgs"],
    }),
  ],
});
export default appStore;
