<template>
  <button @click="preview">预览</button>
  <div
    style="
      inset: 0;
      background-color: rgba(0, 0, 0, 0.5);
      position: fixed;
      text-align: center;
      line-height: 100%;
    "
    @dblclick="dblclick"
  >
    <img
      style="
        position: absolute;

        display: block;
      "
      @mousedown="mousedown"
      @load="load"
      src="data:image/svg+xml;base64,"
      alt=""
    />
    <div @dblclick="dbclick" @blur="blur" style="user-select: none">aaaaa</div>
  </div>
</template>
<script setup>
import { ref, onMounted, reactive, getCurrentInstance } from "vue";
const proxy = getCurrentInstance();
const load = (e) => {
  let parentNode = e.target.parentNode;
  e.target.style.left =
    parentNode.offsetWidth / 2 - e.target.offsetWidth / 2 + "px";
  e.target.style.top =
    parentNode.offsetHeight / 2 - e.target.offsetHeight / 2 + "px";
};
let drag = (e) => {
  e.target.style.cursor = "move";

  console.dir(e.target);
};

const mousedown = (e) => {
  console.log(e);
  console.log(`x${e.pageX}   y${e.pageY}`);
  let x = e.offsetX;
  let y = e.offsetY;
  let parentNode = e.target.parentNode;
  console.log(parentNode.offsetWidth);
  console.log(e.target.offsetTop);
  e.target.onmousemove = (e) => {
    e.target.style.left = e.pageX - x + "px";
    e.target.style.top = e.pageY - y + "px";
    if (e.target.offsetTop < 0) {
      e.target.style.top = 0 + "px";
    } else if (
      e.target.offsetTop >
      parentNode.offsetHeight - e.target.offsetHeight
    ) {
      e.target.style.top =
        parentNode.offsetHeight - e.target.offsetHeight + "px";
    }

    if (e.target.offsetLeft < 0) {
      e.target.style.left = 0 + "px";
    } else if (
      e.target.offsetLeft >
      parentNode.offsetWidth - e.target.offsetWidth
    ) {
      e.target.style.left =
        parentNode.offsetWidth - e.target.offsetWidth + "px";
    }

    // if(e.pageX-x)
  };
  e.target.onmouseup = (e) => {
    e.target.onmousemove = null;
  };
  // e.target.ondragstart = dragstart;
  // e.target.ondragend = dragend;
};

const dbclick = (e) => {
  if (e.target.contentEditable != true) {
    e.target.contentEditable = true;
  } else {
    e.target.contentEditable = false;
  }
};
const blur = (e) => {
  e.target.contentEditable = false;
};
const dblclick = (e) => {
  e.target.style.display = "none";
};
const preview = (e) => {
  e.target.parentNode.children[1].style.display = "block";
};
</script>
<style lang="less" scoped>
img {
  -webkit-user-drag: none;
  cursor: move;
}
</style>
