<template>
  <input v-model="value" type="range" min="0" max="100" />
  <span>{{ value }}</span>
  <div
    @dragover="dragover"
    @dragenter="dragenter"
    @dragleave="dragleave"
    @drop="drop"
    style="
      width: 200px;
      height: 200px;
      border: 1px solid black;
      overflow: auto;
      margin-bottom: 20px;
    "
  >
    <p
      v-for="(i, index) in 10"
      :key="index"
      @dragstart="dragstart"
      draggable="true"
    >
      1234566
    </p>
  </div>
  <div
    @dragover="dragover"
    @dragenter="dragenter"
    @dragleave="dragleave"
    @drop="drop"
    style="width: 200px; height: 200px; border: 1px solid black; overflow: auto"
  ></div>
  <div ref="imgs">
    <img
      v-for="(img, index) in imgList"
      :src="img"
      alt=""
      @contextmenu="methods.contextmenu"
    />
  </div>
</template>
<script setup>
import { ref, onMounted, reactive, getCurrentInstance } from "vue";
const proxy = getCurrentInstance();
const value = ref(0);
let id = 0;
const dragstart = (e) => {
  if (e.target.id == null || e.target.id == "") {
    e.target.id = Math.random().toString(36).substr(2, 9);
    id = e.target.id;
  } else {
    id = e.target.id;
  }

  e.dataTransfer.setData("id", e.target.id);
};
let imgList = ref([]);
let PictureSuffix = ["jpg", "png", "gif", "bmp", "jpeg"];
let methods = reactive({
  contextmenu: null,
});

const dragend = (e) => {};
const drag = (e) => {};
const dragover = (e) => {
  e.preventDefault();

  // if (e.target != document.getElementById(id).parentNode) {
  //   e.target.style.borderWidth = "3px";
  // }
};

const dragenter = (e) => {};
const dragleave = (e) => {};
const drop = (e) => {
  if (e.dataTransfer.files.length > 0) {
    for (let file of e.dataTransfer.files) {
      if (PictureSuffix.includes(file.name.split(".")[1])) {
        let fileReader = new FileReader();
        fileReader.onload = function (e) {
          imgList.value.push(e.target.result);
        };
        fileReader.readAsDataURL(file);
      }
    }
  }
  let element = document.getElementById(e.dataTransfer.getData("id"));
  if (element) {
    e.target.appendChild(element);
    e.target.style.borderWidth = "1px";
  }
};

onMounted(() => {
  document.ondragover = function (e) {
    e.preventDefault(); //只有在ondragover中阻止默认行为才能触发 ondrop 而不是 ondragleave
  };
  document.ondrop = function (e) {
    e.preventDefault(); //阻止 document.ondrop的默认行为  *** 在新窗口中打开拖进的图片
  };
  document.oncontextmenu = function (e) {
    return false;
    //或者 e.preventDefault()
  };
  methods.contextmenu = (e) => {
    console.log("contextmenu");

    let imgss = proxy.refs.imgs;
    let img = e.target;
    console.log(e.target);
    console.log(e.pageX + "   " + e.pageY);
    let menu = document.createElement("div");
    let html = document.querySelector("html");
    menu.classList.add("menu");
    menu.style.position = "fixed";
    menu.style.left = e.pageX - 10 + "px";
    menu.style.top = e.pageY - html.scrollTop - 10 + "px";
    menu.style.width = "60px";
    menu.style.height = "auto";
    menu.style.backgroundColor = "pink";
    menu.style.display = "flex";
    menu.style.justifyContent = "center";
    menu.style.alignItems = "center";
    menu.style.flexDirection = "column";
    let button = document.createElement("button");
    button.innerText = "删除";
    button.style.width = "100%";
    button.onclick = function () {
      imgss.removeChild(img);
      document.body.removeChild(menu);
    };
    menu.appendChild(button);
    document.body.appendChild(menu);
    menu.onmouseleave = function (e) {
      document.body.removeChild(menu);
    };
  };
  //禁止使用F12

  // window.onkeydown =
  //   window.onkeyup =
  //   window.onkeypress =
  //     function (event) {
  //       // 判断是否按下F12，F12键码为123

  //       if (event.keyCode === 123) {
  //         event.preventDefault(); // 阻止默认事件行为

  //         window.event.returnValue = false;
  //       }
  //     };
});
</script>
<style lang="less" scoped>
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
</style>
