<template>
  <div class="container">
    <div class="previewImg" ref="previewImg">
      <img :src="currentUrl" alt="" />
      <div class="mask"></div>
      <div class="bigimg">
        <img :src="currentUrl" alt="" />
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref, reactive, getCurrentInstance, onMounted } from "vue";
let currentUrl = ref("");
import("@/assets/1.jpg").then((res) => {
  currentUrl.value = res.default;
});
let proxy = getCurrentInstance();
onMounted(() => {
  let previewImg = proxy.refs.previewImg;
  let img = proxy.refs.previewImg.querySelector("img");
  let mask = previewImg.querySelector(".mask");
  let bigimg = previewImg.querySelector(".bigimg img");
  previewImg.onmouseenter = () => {
    previewImg.querySelector(".mask").style.display = "block";
    previewImg.querySelector(".bigimg").style.display = "block";
    previewImg.onmousemove = (e) => {
      e.stopPropagation();
      let x =
        e.pageX -
        (previewImg.offsetLeft +
          previewImg.parentNode.offsetLeft +
          previewImg.parentNode.parentNode.offsetLeft) -
        mask.offsetWidth / 2;
      let y =
        e.pageY -
        (previewImg.offsetTop +
          previewImg.parentNode.offsetTop +
          previewImg.parentNode.parentNode.offsetTop) -
        mask.offsetHeight / 2;
      mask.style.left = x + "px";
      mask.style.top = y + "px";
      if (x < 0) {
        mask.style.left = 0 + "px";
      }
      if (x > previewImg.offsetWidth - mask.offsetWidth) {
        mask.style.left = previewImg.offsetWidth - mask.offsetWidth + "px";
      }
      if (y < 0) {
        mask.style.top = 0 + "px";
      }
      if (y > previewImg.offsetHeight - mask.offsetHeight) {
        mask.style.top = previewImg.offsetHeight - mask.offsetHeight + "px";
      }
      let scaleX = bigimg.offsetWidth / img.offsetWidth;
      let scaleY = bigimg.offsetHeight / img.offsetHeight;
      bigimg.style.left = -mask.offsetLeft * scaleX + "px";
      bigimg.style.top = -mask.offsetTop * scaleY + "px";
    };
  };
  previewImg.onmouseleave = () => {
    previewImg.querySelector(".mask").style.display = "none";
    previewImg.querySelector(".bigimg").style.display = "none";
    previewImg.onmousemove = null;
  };
});
</script>
<style lang='less' scoped>
.container {
  width: 100%;
  height: 100%;
  background-color: #fff;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .previewImg {
    width: 400px;
    height: 400px;
    position: relative;
    cursor: move;
    img {
      width: 100%;
      height: 100%;
    }
    position: relative;
    .mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 200px;
      height: 200px;
      background-color: rgba(178, 205, 25, 0.429);
    }
    .bigimg {
      position: absolute;
      top: 0;
      left: 410px;
      width: 400px;
      height: 400px;
      overflow: hidden;
      border: 1px;
      img {
        width: 800px;
        height: 800px;
        position: absolute;
        top: 0;
        left: 0;
        border: none;
      }
    }
  }
}
</style>