<template>
  <div :class="{ main: true }">
    <div class="form" @mouseenter="over" @mouseleave="out" ref="form">
      <div class="bd">
        <h1>登录</h1>
      </div>
      <div class="mask"></div>
    </div>
    <div
      class="animated"
      @mouseenter="mouseenter"
      @mouseleave="mouseenter"
      style="width: 300px; height: 300px; background-color: pink"
    ></div>
  </div>
</template>
<script setup>
import {
  ref,
  reactive,
  computed,
  toRefs,
  watch,
  getCurrentInstance,
  toRaw,
  onMounted,
} from "vue";
const proxy = getCurrentInstance();

let mask;

const over = (e) => {
  console.log(e);
  e.stopPropagation();
  mask = proxy.refs.form.querySelector(".mask");

  mask.style.top = e.offsetY + "px";
  mask.style.left = e.offsetX + "px";

  if (mask.classList.contains("out")) {
    mask.classList.replace("out", "in");
  }
  if (!mask.classList.contains("in")) {
    mask.classList.add("in");
  }

  mask.classList.add("in");
};
const out = (e) => {
  console.log(e);
  e.stopPropagation();
  mask = proxy.refs.form.querySelector(".mask");

  mask.style.top = e.offsetY + "px";
  mask.style.left = e.offsetX + "px";

  mask.classList.replace("in", "out");
};
const mouseenter = (e) => {
  e.target.classList.toggle("shake");
};
onMounted(() => {});
</script>
<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}

.main {
  position: absolute !important;
  background-image: linear-gradient(135deg, #17ead9, #6078ea);
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  .form {
    min-width: 300px;
    min-height: 400px;
    background-color: #1890ff;
    border-radius: 15px;
    position: relative;
    overflow: hidden;

    .mask {
      position: absolute;
      width: 0px;
      height: 0px;
      background-color: pink;
      z-index: 2;
      border-radius: 50%;
      transform: translate(-50%, -50%);
    }
    .in {
      animation: in 1s ease-in-out forwards;
    }
    .out {
      animation: out 1s ease-in-out forwards;
    }

    .bd {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 3;
      background-color: transparent;
    }
  }
}
@keyframes in {
  0% {
    width: 0px;
    height: 0px;
  }
  100% {
    width: 1200px;
    height: 1200px;
  }
}
@keyframes out {
  0% {
    width: 1200px;
    height: 1200px;
  }
  100% {
    width: 0px;
    height: 0px;
  }
}
</style>